import React, { Component } from "react";
import Node from "./Node/Node";
import InfoBox from "./Ui/InfoBox";

import "./PathVisualizer.css";

import { dijkstra, getShortestPath } from "../algorithms/dijkstra";
import { astar } from "../algorithms/astar";
import {
  bidijkstra,
  getShortestPathBidirectional,
} from "../algorithms/bi-dijkstra";
import { bfs } from "../algorithms/bfs";

const ACTION = {
  WALL: "wall",
  START: "start",
  FINISH: "finish",
  ROAD: "road",
  DELETE: "",
};

const ALGOS = {
  DIJKSTRA: "dijkstra",
  ASTAR: "a-star",
  BIDIJKSTRA: "bi-dijkstra",
  BFS: "bfs",
};

const SPEED = {
  SLOW: 400,
  MEDIUM: 200,
  FAST: 100,
};

export default class PathfindingVisualizer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      grid: [],
      mouseIsPressed: false,
      mouseAction: ACTION.WALL,
      startNode: { y: 2, x: 3 },
      finishNode: { y: 7, x: 8 },
    };
  }

  componentDidMount() {
    const grid = this.getStartGrid();
    this.setState({ grid });
  }

  handleMouseDown(posX, posY) {
    const action = this.state.mouseAction;
    if (
      action === ACTION.WALL ||
      action === ACTION.ROAD ||
      action === ACTION.DELETE
    ) {
      const newGrid = getUpdateWallRoadGrid(
        this.state.grid,
        posX,
        posY,
        action
      );
      this.setState({ grid: newGrid, mouseIsPressed: true });
    }
    if (action === ACTION.START || action === ACTION.FINISH) {
      const newGrid = this.getUpdateStartFinishGrid(posX, posY, action);
      this.setState({ grid: newGrid });
    }
  }

  handleMouseEnter(posX, posY) {
    const action = this.state.mouseAction;
    if (!this.state.mouseIsPressed) return;
    if (
      action === ACTION.WALL ||
      action === ACTION.ROAD ||
      action === ACTION.DELETE
    ) {
      const newGrid = getUpdateWallRoadGrid(
        this.state.grid,
        posX,
        posY,
        action
      );
      this.setState({ grid: newGrid });
    }
  }

  handleMouseUp() {
    const selectValue = document.getElementById("draw-selector").value;
    this.setState({ mouseIsPressed: false, mouseAction: selectValue });
  }

  animateDijkstra(visitedNodesInOrder, nodesInShortestPath) {
    for (let i = 0; i <= visitedNodesInOrder.length; i++) {
      //at last i start animateShortestPath
      if (i === visitedNodesInOrder.length) {
        setTimeout(() => {
          this.addNodeVisited(visitedNodesInOrder[i - 1]);
          this.animateShortestPath(nodesInShortestPath);
        }, SPEED.FAST * i);
        return;
      }
      if (i < visitedNodesInOrder.length) {
        //check if node is not start or finish
        setTimeout(() => {
          this.addNodeFocus(visitedNodesInOrder[i]);
        }, SPEED.FAST * i);
        //when i bigger 0 then change i-1 to is visited
        if (i > 0) {
          setTimeout(() => {
            this.addNodeVisited(visitedNodesInOrder[i - 1]);
          }, SPEED.FAST * i);
        }
      }
    }
  }

  animateDijkstraBidirectional(
    visitedNodesInOrder,
    visitedNodesInOrderBi,
    nodesInShortestPath
  ) {
    for (let i = 0; i <= visitedNodesInOrder.length; i++) {
      if (i === visitedNodesInOrder.length) {
        setTimeout(() => {
          this.addNodeVisited(visitedNodesInOrder[i - 1]);
        }, 200 * i);
      } else if (i < visitedNodesInOrder.length) {
        setTimeout(() => {
          this.addNodeFocus(visitedNodesInOrder[i]);
        }, 200 * i);
        //when i bigger 0 then change i-1 to is visited
        if (i > 0) {
          setTimeout(() => {
            this.addNodeVisited(visitedNodesInOrder[i - 1]);
          }, 200 * i);
        }
      }
    }

    for (let i = 0; i <= visitedNodesInOrderBi.length; i++) {
      //at last i start animateShortestPath
      if (i === visitedNodesInOrderBi.length) {
        setTimeout(() => {
          this.addNodeVisitedBi(visitedNodesInOrderBi[i - 1]);
        }, 100 + 200 * i);
        setTimeout(() => {
          this.animateShortestPath(nodesInShortestPath);
        }, 200 + 200 * i);
      } else if (i < visitedNodesInOrderBi.length) {
        setTimeout(() => {
          this.addNodeFocusBi(visitedNodesInOrderBi[i]);
        }, 100 + 200 * i);
        //when i bigger 0 then change i-1 to is visited
        if (i > 0) {
          setTimeout(() => {
            this.addNodeVisitedBi(visitedNodesInOrderBi[i - 1]);
          }, 100 + 200 * i);
        }
      }
    }
  }

  addNodeFocus(visitedNode) {
    if (checkClassnameChangeable(visitedNode)) {
      const node = visitedNode;
      const nodeElement = document.getElementById(
        `node-${node.posX}-${node.posY}`
      );
      nodeElement.classList.add("node-focus");
    }
  }

  addNodeFocusBi(visitedNode) {
    if (checkClassnameChangeable(visitedNode)) {
      const node = visitedNode;
      const nodeElement = document.getElementById(
        `node-${node.posX}-${node.posY}`
      );
      nodeElement.classList.add("node-focus-bi");
    }
  }

  addNodeVisited(visitedNode) {
    if (checkClassnameChangeable(visitedNode)) {
      const node = visitedNode;
      const nodeElement = document.getElementById(
        `node-${node.posX}-${node.posY}`
      );

      if (nodeElement.classList.contains("node-focus")) {
        nodeElement.classList.remove("node-focus");
      }
      if (node.type === ACTION.ROAD) {
        nodeElement.classList.add("node-visited-road");
      }
      nodeElement.classList.add("node-visited");
    }
  }

  addNodeVisitedBi(visitedNode) {
    if (checkClassnameChangeable(visitedNode)) {
      const node = visitedNode;
      const nodeElement = document.getElementById(
        `node-${node.posX}-${node.posY}`
      );

      if (nodeElement.classList.contains("node-focus-bi")) {
        nodeElement.classList.remove("node-focus-bi");
      }
      if (node.type === ACTION.ROAD) {
        nodeElement.classList.add("node-visited-road");
      }
      nodeElement.classList.add("node-visited-bi");
    }
  }

  animateShortestPath(nodesInShortestPath) {
    for (let i = 0; i < nodesInShortestPath.length; i++) {
      if (checkClassnameChangeable(nodesInShortestPath[i])) {
        setTimeout(() => {
          const node = nodesInShortestPath[i];
          const nodeElement = document.getElementById(
            `node-${node.posX}-${node.posY}`
          );
          const rowOffset = node.rowOffset ? "row-offset" : "";
          if (node.type === ACTION.ROAD) {
            nodeElement.className = "node node-shortest-path-road " + rowOffset;
          } else {
            nodeElement.className = "node node-shortest-path " + rowOffset;
          }
        }, SPEED.FAST * i);
      }
    }
  }

  startAlgo() {
    this.clear(false);
    const algo = document.getElementById("algo-selector").value;
    if (algo === ALGOS.DIJKSTRA) {
      this.visualizeDijkstra();
    } else if (algo === ALGOS.ASTAR) {
      this.visualizeAStar();
    } else if (algo === ALGOS.BIDIJKSTRA) {
      this.visualizeBiDijkstra();
    } else if (algo === ALGOS.BFS) {
      this.visualizeBFS();
    }
  }

  visualizeDijkstra() {
    const { grid } = this.state;

    const startPos = this.state.startNode;
    const finishPos = this.state.finishNode;

    const visitedNodesInOrder = dijkstra(grid, startPos);

    const finishNode = grid[finishPos.y][finishPos.x];
    const nodesInShortestPath = getShortestPath(finishNode);

    this.animateDijkstra(visitedNodesInOrder, nodesInShortestPath);
  }

  visualizeAStar() {
    const { grid } = this.state;
    const startPos = this.state.startNode;
    const finishPos = this.state.finishNode;

    const visitedNodesInOrder = astar(grid, startPos, finishPos);

    const finishNode = grid[finishPos.y][finishPos.x];
    const nodesInShortestPath = getShortestPath(finishNode);

    this.animateDijkstra(visitedNodesInOrder, nodesInShortestPath);
  }

  visualizeBiDijkstra() {
    const { grid } = this.state;
    const startPos = this.state.startNode;
    const finishPos = this.state.finishNode;

    const {
      visitedNodesInOrder,
      visitedNodesInOrderBi,
      closestNode,
      finishNode2,
    } = bidijkstra(grid, startPos, finishPos);

    document.getElementById("labelNodesVisited").innerHTML =
      visitedNodesInOrder.length -
      1 +
      visitedNodesInOrderBi.length -
      1 +
      " Nodes";

    const nodesInShortestPath =
      closestNode == null || finishNode2 == null
        ? []
        : getShortestPathBidirectional(closestNode, finishNode2);

    this.animateDijkstraBidirectional(
      visitedNodesInOrder,
      visitedNodesInOrderBi,
      nodesInShortestPath
    );
  }

  visualizeBFS() {
    const { grid } = this.state;
    const startPos = this.state.startNode;
    const finishPos = this.state.finishNode;

    const visitedNodesInOrder = bfs(grid, startPos);

    const finishNode = grid[finishPos.y][finishPos.x];
    const nodesInShortestPath = getShortestPath(finishNode);

    this.animateDijkstra(visitedNodesInOrder, nodesInShortestPath);
  }

  setAction(action) {
    this.setState({ mouseAction: action });
  }

  //clearAll -> when true clear Walls and Roads
  clear(clearAll) {
    const newGrid = this.state.grid;
    for (const y of newGrid) {
      for (const x of y) {
        const newType =
          clearAll && (x.type === ACTION.WALL || x.type === ACTION.ROAD)
            ? ""
            : x.type;

        const node = {
          ...x,
          isVisited: false,
          isVisitedBi: false,
          type: newType,
        };
        newGrid[x.posY][x.posX] = node;
        const nodeElement = document.getElementById(
          `node-${node.posX}-${node.posY}`
        );
        const offset = x.rowOffset ? "row-offset" : "";
        nodeElement.className = "node " + offset + " " + x.type;
      }
    }
    this.setState({ grid: newGrid });

    clearLabels();
  }

  clearWallRoad() {
    const newGrid = this.getStartGrid();
    for (const y of newGrid) {
      for (const x of y) {
        const node = {
          ...x,
          isVisited: false,
          type: x.type === ACTION.WALL ? "" : x.type,
        };
        newGrid[x.posY][x.posX] = node;
        const nodeElement = document.getElementById(
          `node-${node.posX}-${node.posY}`
        );
        const offset = x.rowOffset ? "row-offset" : "";
        nodeElement.className = "node " + offset + " " + x.type;
      }
    }
    this.setState({ grid: newGrid });
  }

  changeDrawType(selectObj) {
    const value = selectObj.value;
    this.setState({ mouseAction: value });
  }

  render() {
    const { grid, mouseIsPressed } = this.state;
    return (
      <div className="noscroll">
        <div className={"menu"}>
          <div className={"box"}>
            <select id={"algo-selector"} defaultValue={ALGOS.DIJKSTRA}>
              <option value={ALGOS.DIJKSTRA}>Dijkstra</option>
              <option value={ALGOS.ASTAR}>A*</option>
              <option value={ALGOS.BIDIJKSTRA}>Bi-Dijkstra</option>
              <option value={ALGOS.BFS}>BFS</option>
            </select>
          </div>
          <div className={"box"}>
            <select
              id={"draw-selector"}
              onChange={() => this.changeDrawType(this)}
              defaultValue={ACTION.WALL}
            >
              <option value={ACTION.WALL}>Wall</option>
              <option value={ACTION.ROAD}>Road</option>
              <option value={ACTION.DELETE}>Delete</option>
            </select>
          </div>
          <button className={"white-btn"} onClick={() => this.clear(false)}>
            Clear Path
          </button>
          <button className={"startButton"} onClick={() => this.startAlgo()}>
            Start Search!
          </button>
          <button className={"white-btn"} onClick={() => this.clear(true)}>
            Clear ALL
          </button>
          <button
            id={"setStartBtn"}
            className={"btn"}
            onClick={() => this.setAction(ACTION.START)}
          >
            SetStart
          </button>
          <button
            id={"setFinishBtn"}
            className={"btn"}
            onClick={() => this.setAction(ACTION.FINISH)}
          >
            SetFinish
          </button>
        </div>

        <InfoBox />

        <div className="grid" id="griddiv">
          {grid.map((x, xId) => {
            return (
              <div key={xId}>
                {x.map((node, nodeId) => {
                  const { posX, posY, type, distance } = node;
                  return (
                    <Node
                      rowOffset={xId % 2 === 0 ? false : true}
                      key={nodeId}
                      type={type}
                      posX={posX}
                      posY={posY}
                      onMouseDown={(posX, posY) =>
                        this.handleMouseDown(posX, posY)
                      }
                      onMouseEnter={(posX, posY) =>
                        this.handleMouseEnter(posX, posY)
                      }
                      onMouseUp={() => this.handleMouseUp()}
                      mouseIsPressed={mouseIsPressed}
                      distance={distance}
                    ></Node>
                  );
                })}
              </div>
            );
          })}
        </div>
      </div>
    );
  }

  createNode(posX, posY) {
    return {
      posX,
      posY,
      type:
        posX === this.state.startNode.x && posY === this.state.startNode.y
          ? "start"
          : posX === this.state.finishNode.x && posY === this.state.finishNode.y
          ? "finish"
          : "",
      distance: Infinity,
      isVisited: false,
      isVisitedBi: false,
      previousNode: null,
      finishDistance: Infinity,
      rowOffset: posY % 2 === 0 ? false : true,
    };
  }

  getStartGrid() {
    const grid = [];

    let canvas = document.getElementById("griddiv");
    //console.log(canvas.getBoundingClientRect().width);

    const { width, height } = canvas.getBoundingClientRect();

    let yAmount = 5;
    let xAmount = 5;

    if (width <= 600) {
      yAmount = height / 32;
      xAmount = width / 36;
    } else {
      xAmount = width / 52;
      yAmount = height / 40;
    }

    for (let y = 0; y < yAmount; y++) {
      const row = [];

      for (let x = 0; x < xAmount; x++) {
        const node = this.createNode(x, y);
        row.push(node);
      }
      grid.push(row);
    }
    return grid;
  }

  getUpdateStartFinishGrid(posX, posY, action) {
    const newGrid = this.state.grid;

    let oldX = this.state.finishNode.x;
    let oldY = this.state.finishNode.y;

    if (action === ACTION.START) {
      oldX = this.state.startNode.x;
      oldY = this.state.startNode.y;
    }

    const oldNode = newGrid[oldY][oldX];
    const newNode = {
      ...oldNode,
      type: "",
    };
    newGrid[oldY][oldX] = newNode;

    const newStart = newGrid[posY][posX];
    const theNewStart = {
      ...newStart,
      type: action,
    };
    newGrid[posY][posX] = theNewStart;

    if (action === ACTION.START) {
      this.setState({ startNode: { y: posY, x: posX } });
      return newGrid;
    } else {
      this.setState({ finishNode: { y: posY, x: posX } });
      return newGrid;
    }
  }
}

const getUpdateWallRoadGrid = (grid, x, y, action) => {
  const newGrid = grid.slice();
  const node = newGrid[y][x];
  if (node.type === "start" || node.type === "finish") {
    return newGrid;
  }
  const newNode = {
    ...node,
    type: action,
  };
  newGrid[y][x] = newNode;
  return newGrid;
};

const checkClassnameChangeable = (node) => {
  if (node.type === "start" || node.type === "finish") {
    return false;
  }
  return true;
};

const clearLabels = () => {
  document.getElementById("labelAlgorithm").innerHTML =
    document.getElementById("algo-selector").value;
  document.getElementById("labelSpeed").innerHTML = "--";
  document.getElementById("labelNodesVisited").innerHTML = "--";
  document.getElementById("labelPathLength").innerHTML = "--";
  document.getElementById("labelPathCost").innerHTML = "--";
};
