import { getAllNodes, resetNodes, getUnvisitedNeighbors } from "./dijkstra.js";

export function bfs(grid, startPos) {
  document.getElementById("labelAlgorithm").innerHTML = "Dijkstra";
  const startTime = window.performance.now();

  const visitedNodesInOrder = [];

  const algoGrid = resetNodes(grid);
  const startNode = algoGrid[startPos.y][startPos.x];

  startNode.distance = 0;

  let index = 0;
  visitedNodesInOrder.push(startNode);

  while (index < visitedNodesInOrder.length) {
    const neighbors = getUnvisitedNeighbors(
      visitedNodesInOrder[index],
      algoGrid
    );
    console.log("index: " + index);
    console.log(neighbors);

    for (const neighbor of neighbors) {
      const condition = visitedNodesInOrder.some(
        (n) => n.posX === neighbor.posX && n.posY === neighbor.posY
      );
      console.log(condition);

      if (!condition) {
        console.log("push");
        visitedNodesInOrder.push(neighbor);
        neighbor.previousNode = visitedNodesInOrder[index];

        if (neighbor.type === "finish") {
          const endTime = window.performance.now();
          const time = endTime - startTime;

          document.getElementById("labelSpeed").innerHTML =
            time.toFixed(2) + " ms";
          document.getElementById("labelNodesVisited").innerHTML =
            visitedNodesInOrder.length - 1 + " Nodes";
          return visitedNodesInOrder;
        }
      }
    }
    index++;
  }
  const endTime = window.performance.now();
  const time = endTime - startTime;

  document.getElementById("labelSpeed").innerHTML = time.toFixed(2) + " ms";
  document.getElementById("labelNodesVisited").innerHTML =
    visitedNodesInOrder.length - 1 + " Nodes";
  return visitedNodesInOrder;
}
