export function dijkstra(grid, startPos) {
  document.getElementById("labelAlgorithm").innerHTML = "Dijkstra";
  const startTime = window.performance.now();
  const visitedNodesInOrder = [];

  const algoGrid = resetNodes(grid);
  const startNode = algoGrid[startPos.y][startPos.x];

  startNode.distance = 0;

  const unvisitedNodes = getAllNodes(algoGrid);

  while (!!unvisitedNodes.length) {
    sortNodesByDistance(unvisitedNodes);
    const closestNode = unvisitedNodes.shift();
    //skip wall
    if (closestNode.type === "wall") continue;

    //when closest node has distance infinity we are stuck -> stop
    if (closestNode.distance === Infinity) {
      const endTime = window.performance.now();
      const time = endTime - startTime;

      document.getElementById("labelSpeed").innerHTML = time.toFixed(2) + " ms";
      document.getElementById("labelNodesVisited").innerHTML =
        visitedNodesInOrder.length - 1 + " Nodes";
      return visitedNodesInOrder;
    }

    closestNode.isVisited = true;
    visitedNodesInOrder.push(closestNode);

    if (closestNode.type === "finish") {
      const endTime = window.performance.now();
      const time = endTime - startTime;

      document.getElementById("labelSpeed").innerHTML = time.toFixed(2) + " ms";
      document.getElementById("labelNodesVisited").innerHTML =
        visitedNodesInOrder.length - 1 + " Nodes";
      return visitedNodesInOrder;
    }

    updateUnvisitedNeighbors(closestNode, algoGrid);
  }
}

export function getAllNodes(grid) {
  const nodes = [];
  for (const y of grid) {
    for (const node of y) {
      nodes.push(node);
    }
  }
  return nodes;
}

export function sortNodesByDistance(unvisitedNodes) {
  unvisitedNodes.sort((nodeA, nodeB) => nodeA.distance - nodeB.distance);
}

export function updateUnvisitedNeighbors(node, grid) {
  const unvisitedNeighbors = getUnvisitedNeighbors(node, grid);
  for (const neighbor of unvisitedNeighbors) {
    const cost = neighbor.type === "road" ? 1 : 4;
    if (neighbor.distance >= node.distance + cost) {
      neighbor.distance = node.distance + cost;
      neighbor.previousNode = node;
    }
  }
}

export function getUnvisitedNeighbors(node, grid) {
  const neighbors = [];
  const { posY, posX } = node;

  if (posX > 0) neighbors.push(grid[posY][posX - 1]);
  if (posY > 0) neighbors.push(grid[posY - 1][posX]);
  if (posY < grid.length - 1) neighbors.push(grid[posY + 1][posX]);
  if (posX < grid[0].length - 1) neighbors.push(grid[posY][posX + 1]);
  if (node.posY % 2 !== 0) {
    if (posY < grid.length - 1 && posX < grid[0].length - 1)
      neighbors.push(grid[posY + 1][posX + 1]);
    if (posY > 0 && posX < grid[0].length - 1)
      neighbors.push(grid[posY - 1][posX + 1]);
  } else {
    if (posY < grid.length - 1 && posX > 0)
      neighbors.push(grid[posY + 1][posX - 1]);
    if (posY > 0 && posX > 0) neighbors.push(grid[posY - 1][posX - 1]);
  }
  return neighbors.filter((neighbor) => !(neighbor.type === "wall"));
  //return neighbors.filter((neighbor) => !neighbor.isVisited);
}

export function resetNodes(grid) {
  const newGrid = grid.slice();

  for (const y of grid) {
    for (const x of y) {
      const newNode = {
        ...x,
        distance: Infinity,
        finishDistance: Infinity,
        previousNode: null,
      };
      newGrid[x.posY][x.posX] = newNode;
    }
  }
  return newGrid;
}

export function getShortestPath(finishNode) {
  const shortestPath = [];
  let currentNode = finishNode;
  let pathcost = finishNode.distance;
  while (currentNode !== null) {
    shortestPath.unshift(currentNode);
    currentNode = currentNode.previousNode;
  }
  document.getElementById("labelPathLength").innerHTML =
    shortestPath.length - 1 + "";

  document.getElementById("labelPathCost").innerHTML = pathcost + "";
  return shortestPath;
}
