import React, { Component } from "react";

import "./Node.css";

export default class Node extends Component {
  render() {
    const {
      rowOffset,
      type,
      posX,
      posY,
      onMouseDown,
      onMouseEnter,
      onMouseUp,
    } = this.props;

    const classOffset = rowOffset ? "row-offset" : "";

    return (
      <div
        id={`node-${posX}-${posY}`}
        className={`node ${classOffset} ${type}`}
        onMouseDown={() => onMouseDown(posX, posY)}
        onMouseEnter={() => onMouseEnter(posX, posY)}
        onMouseUp={() => onMouseUp()}
      ></div>
    );
  }
}
