import { getAllNodes, resetNodes, getUnvisitedNeighbors } from "./dijkstra.js";

export function astar(grid, startPos, finishPos) {
  document.getElementById("labelAlgorithm").innerHTML = "A*";
  const startTime = window.performance.now();
  const visitedNodesInOrder = [];

  const algoGrid = resetNodes(grid);

  const startNode = algoGrid[startPos.y][startPos.x];
  const finishNode = algoGrid[finishPos.y][finishPos.x];

  startNode.distance = 0;
  setFinishDist(startNode, finishNode);

  const unvisitedNodes = getAllNodes(algoGrid);

  while (!!unvisitedNodes.length) {
    sortNodesByFinishDistance(unvisitedNodes);
    const closestNode = unvisitedNodes.shift();
    //skip wall
    if (closestNode.type === "wall") continue;

    //when closest node has distance infinity we are stuck -> stop
    if (closestNode.distance === Infinity) {
      const endTime = window.performance.now();
      const time = endTime - startTime;

      document.getElementById("labelSpeed").innerHTML = time.toFixed(2) + " ms";
      document.getElementById("labelNodesVisited").innerHTML =
        visitedNodesInOrder.length - 1 + " Nodes";
      return visitedNodesInOrder;
    }

    closestNode.isVisited = true;
    visitedNodesInOrder.push(closestNode);

    if (closestNode.type === "finish") {
      const endTime = window.performance.now();
      const time = endTime - startTime;

      document.getElementById("labelSpeed").innerHTML = time.toFixed(2) + " ms";
      document.getElementById("labelNodesVisited").innerHTML =
        visitedNodesInOrder.length - 1 + " Nodes";
      return visitedNodesInOrder;
    }

    updateUnvisitedNeighbors(closestNode, algoGrid, finishNode);
  }
}

function updateUnvisitedNeighbors(node, grid, finishNode) {
  const unvisitedNeighbors = getUnvisitedNeighbors(node, grid);
  for (const neighbor of unvisitedNeighbors) {
    if (neighbor.finishDistance === Infinity)
      setFinishDist(neighbor, finishNode);
    const cost = neighbor.type === "road" ? 1 : 4;
    if (neighbor.distance >= node.distance + cost) {
      neighbor.distance = node.distance + cost;
      neighbor.previousNode = node;
    }
  }
}

function setFinishDist(node, finishNode) {
  const dist =
    Math.round(
      Math.sqrt(
        Math.pow(node.posX - finishNode.posX, 2) +
          Math.pow(node.posY - finishNode.posY, 2),
        2
      )
    ) * 4;
  node.finishDistance = dist;
}

function sortNodesByFinishDistance(unvisitedNodes) {
  unvisitedNodes.sort(
    (nodeA, nodeB) =>
      nodeA.distance +
      nodeA.finishDistance -
      (nodeB.finishDistance + nodeB.distance)
  );
}
