import React, { Component } from "react";

import "./InfoBox.css";

export default class InfoBox extends Component {
  render() {
    return (
      <div className="infobox">
        <div className="info">
          <div className="info-top">
            <div className="infoFlex">
              <label id="algo-text">Algorithm:</label>
              <label id="labelAlgorithm">--</label>
            </div>
          </div>
          <div className="info-center">
            <div className="info-left info-side">
              <div className="infoFlex">
                <label>Speed:</label>
                <label id="labelSpeed">--</label>
              </div>
              <div className="infoFlex">
                <label>NodesVisited:</label>
                <label id="labelNodesVisited">--</label>
              </div>
            </div>
            <div className="info-right info-side">
              <div className="infoFlex">
                <label>Path-Length:</label>
                <label id="labelPathLength">--</label>
              </div>
              <div className="infoFlex">
                <label>Path-Cost:</label>
                <label id="labelPathCost">--</label>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
